.popup__field {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  margin: 54px 0 0;
  width: 358px;
  height: 27px;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
}

.popup__field:last-of-type {
  margin: 30px 0 0;
}

@media screen and (max-width: 680px) {
  .popup__field {
    width: 238px;
    margin: 66px 0 0;
  }
}